import React from "react";
import PropTypes from "prop-types";
import { Line } from "react-lineto";

const CELL_SIZE = 100;
let enteredFrom = "";
let exitTo = "";
let lines = [];

export default class Cell extends React.Component {
  state = {
    cellData: this.getConnectionPoints(this.props.value),
    entered: -1
  };

  getValue() {
    const { value } = this.props;

    if (!value.isRevealed) {
      return this.props.value.isFlagged ? "ðŸš©" : null;
    }
    if (value.isMine) {
      return "ðŸ’£";
    }
    if (value.neighbour === 0) {
      return null;
    }
    return value.neighbour;
  }

  getConnectionPoints(value) {
    let data = {};
    data.top = value.row * CELL_SIZE;
    data.left = value.col * CELL_SIZE;
    data.centroid = {
      x: data.left + CELL_SIZE / 2,
      y: data.top + CELL_SIZE / 2
    };
    data.edge = {
      top: { x: data.centroid.x, y: data.centroid.y - CELL_SIZE / 2 },
      bottom: { x: data.centroid.x, y: data.centroid.y + CELL_SIZE / 2 },
      left: { x: data.centroid.x - CELL_SIZE / 2, y: data.centroid.y },
      right: { x: data.centroid.x + CELL_SIZE / 2, y: data.centroid.y }
    };
    return data;
  }

  handleMouseEnter() {
    const { isMouseDown, position, value } = this.props;
    if (isMouseDown) {
      console.log(value.row, value.col);
      const { edge } = this.state.cellData;
      let distFromEdges = {
        left: position.x - edge.left.x,
        right: edge.right.x - position.x,
        top: position.y - edge.top.y,
        bottom: edge.bottom.y - position.y
      };
      let nearestDist = 1000;
      var keys = Object.keys(distFromEdges);
      for (var i = 0; i < keys.length; i++) {
        var key = keys[i];
        if (distFromEdges[key] < nearestDist) {
          nearestDist = distFromEdges[key];
          enteredFrom = key;
        }
      }
      console.log("Entry", enteredFrom);
    }
  }

  handleMouseExit() {
    const { isMouseDown, value, position } = this.props;
    if (isMouseDown) {
      const { edge } = this.state.cellData;
      let distFromEdges = {
        left: position.x - edge.left.x,
        right: edge.right.x - position.x,
        top: position.y - edge.top.y,
        bottom: edge.bottom.y - position.y
      };
      let nearestDist = 1000;
      var keys = Object.keys(distFromEdges);
      for (var i = 0; i < keys.length; i++) {
        var key = keys[i];
        if (distFromEdges[key] < nearestDist) {
          nearestDist = distFromEdges[key];
          exitTo = key;
        }
      }
      const enter = edge[enteredFrom];
      const exit = edge[exitTo];
      lines.push({ enter, exit });
      console.log("Exit", exitTo);
      console.log(lines);
    }
  }

  drawLine() {
    const OFFSET = 40;
    return lines.map(line => (
      <Line
        x0={line.enter.x + OFFSET}
        y0={line.enter.y + OFFSET}
        x1={line.exit.x + OFFSET}
        y1={line.exit.y + OFFSET}
      />
    ));
  }

  render() {
    const { value, onClick, cMenu } = this.props;
    let className =
      "cell" +
      (value.isRevealed ? "" : " hidden") +
      (value.isMine ? " is-mine" : "") +
      (value.isFlagged ? " is-flag" : "");

    return (
      <div
        onClick={onClick}
        className={className}
        onContextMenu={cMenu}
        onMouseEnter={() => this.handleMouseEnter()}
        onMouseOut={() => this.handleMouseExit()}
      >
        {this.getValue()}
        {this.drawLine()}
      </div>
    );
  }
}

const cellItemShape = {
  isRevealed: PropTypes.bool,
  isMine: PropTypes.bool,
  isFlagged: PropTypes.bool
};

Cell.propTypes = {
  value: PropTypes.objectOf(PropTypes.shape(cellItemShape)),
  onClick: PropTypes.func,
  cMenu: PropTypes.func
};
